import LIST_DATA_QUERY from '@/graphql/queries/ListData.gql';
import { AppState } from '@/store/app/types';
import { RootState } from '@/store/types';
import { ActionTree } from 'vuex';
import { apolloClient } from '@/plugins/apollo';

export default {
  async getListData({ commit, state }) {
    commit('setLoading', true);
    const response = await apolloClient.query({
      query: LIST_DATA_QUERY,
      variables: {
        withinDays: 7,
        agentId: state.user.id || 'X0660',
      },
    });
    commit('setListData', response.data.listLifeApps.apps);
    commit('setLoading', false);
  },
} as ActionTree<AppState, RootState>;
