import { MutationTree } from 'vuex';
import { AppData, AppState, AppUser, Error } from '@/store/app/types';

export default {
  setErrors(state: AppState, errors: Error[]) {
    state.errors = errors;
  },

  setLoading(state: AppState, loading: boolean) {
    state.isLoading = loading;
  },

  setUser(state: AppState, user: AppUser) {
    state.user = user;
  },

  setListData(state: AppState, apps: AppData[]) {
    state.listData = apps;
  },
} as MutationTree<AppState>;
