import Vue from 'vue';
import auth from '@/plugins/auth';
import store from '@/store';
import config from '@/plugins/config';

// import messaging from '@/shared-components/plugins/messaging';

import VueApollo from 'vue-apollo';
import { APP } from '@/store/constants';
import { Error } from '@/store/app/types';
import { onError } from 'apollo-link-error';
import { HttpLink } from 'apollo-link-http';
import { setContext } from 'apollo-link-context';
import { ApolloClient } from 'apollo-client';
import { InMemoryCache } from 'apollo-cache-inmemory';

Vue.use(VueApollo);

const httpLink = new HttpLink({
  uri: config?.endpoints?.bff,
});

// control link to add custom app headers (i.e. revision, version and correlation id)
const controlLink = setContext(async (_, { headers }) => {
  const appHeaders = store.getters[`${APP}/headers`];
  const clHeaders = {
    ...headers,
    ...appHeaders,
  };
  const token = await auth.token();
  if (token) {
    clHeaders.authorization = `Bearer ${token}`;
  }
  return {
    headers: clHeaders,
  };
});

// auth link to add JWT to BFF calls
const authLink = setContext(async (_, { headers }) => {
  // get the authentication token from the auth module
  const token = await auth.token();
  const alHeaders = { ...headers };
  if (token) {
    alHeaders.authorization = token ? `Bearer ${token}` : '';
  }
  return {
    headers: alHeaders,
  };
});

// Add necessary BFF / GraphQL error handling logic to this funciton
const errorLink = onError(({ graphQLErrors, networkError }) => {
  let errors: Error[] = [];
  if (graphQLErrors) {
    for (let i = 0; i < graphQLErrors.length; i++) {
      let error = graphQLErrors[i] as any; // eslint-disable-line
      if (error.extensions) {
        error = error.extensions;
        if (graphQLErrors[i].message) {
          error.errorMessage = error.message;
          error.message = graphQLErrors[i].message;
        }
      }
      if (!errors.find(e => e.message === error.message)) {
        errors.push(error);
      }
    }
  } else if (networkError) {
    errors = [
      {
        message: networkError.message as string,
      },
    ];
  }
  // TODO: figure out how to include shared components, plugins, etc.
  // shared messaging plugin displays error messages appropriately
  // messaging.sendError({ errors });
  store.commit(`${APP}/setLoading`, false);
});

// create the apollo client with options to disable caching

export const apolloClient = new ApolloClient({
  link: errorLink.concat(authLink.concat(controlLink.concat(httpLink))),
  cache: new InMemoryCache({
    addTypename: true,
  }),
  defaultOptions: {
    query: {
      fetchPolicy: 'no-cache',
    },
    watchQuery: {
      fetchPolicy: 'no-cache',
    },
  },
  connectToDevTools: true,
});

export const apolloProvider = new VueApollo({
  defaultClient: apolloClient,
});
