import { RootState } from '@/store/types';
import { GetterTree } from 'vuex';
import {
  AppData,
  AppHeaders,
  AppState,
  AppUser,
  Error,
} from '@/store/app/types';

export default {
  errors(state: AppState): Error[] {
    return state.errors;
  },

  headers(state: AppState): AppHeaders {
    return state.headers;
  },

  isLoading(state: AppState): boolean {
    return state.isLoading;
  },

  listData(state: AppState): AppData[] {
    return state.listData;
  },

  user(state: AppState): AppUser {
    return state.user;
  },
} as GetterTree<AppState, RootState>;
