const generateUniqid = () => {
  return Math.random()
    .toString(36)
    .slice(-8);
};

export const uniqid = (length: number): string => {
  let uniqid = '';
  const iterations = Math.ceil(length / 8);
  for (let i = 0; i < iterations; i++) {
    uniqid += generateUniqid().substr(
      0,
      i + 1 === iterations && length % 8 !== 0 ? length % 8 : 8,
    );
  }
  return uniqid;
};
