import { UIState } from '@/store/ui/types';
import { MutationTree } from 'vuex';

export default {
  setLocale(state: UIState, locale: string) {
    state.locale = locale;
  },

  setLeftHandNav(state: UIState, leftHandNav: boolean) {
    state.leftHandNav = leftHandNav;
  },
} as MutationTree<UIState>;
