import { UIState } from '@/store/ui/types';
import { RootState } from '@/store/types';
import { GetterTree } from 'vuex';

export default {
  locale(state: UIState): string {
    return state.locale;
  },

  leftHandNav(state: UIState): boolean {
    return state.leftHandNav;
  },
} as GetterTree<UIState, RootState>;
