import Vue from 'vue';
import axios from 'axios';
import store from '@/store';
import config from '@/plugins/config';
import jwtDecode from 'jwt-decode';
import { APP } from '@/store/constants';
import { uniqid } from '@/shared/utils';
import { AuthToken } from '@/store/app/types';

const SESSION_KEY_PREFIX = process.env.VUE_APP_SESSION_KEY_PREFIX;

const getSessionItem = (key: string) => {
  return sessionStorage.getItem(`${SESSION_KEY_PREFIX}${key}`);
};

const removeSessionItem = (key: string) => {
  sessionStorage.removeItem(`${SESSION_KEY_PREFIX}${key}`);
};

const setSessionItem = (key: string, value: string) => {
  sessionStorage.setItem(`${SESSION_KEY_PREFIX}${key}`, value);
};

// utility to get a query parameter given the key
const getQueryParam = (key: string) => {
  return new URLSearchParams(window.location.search).get(key);
};

const setUser = (idToken: string) => {
  if (!store.getters[`${APP}/user`].name) {
    const decodedToken: any = jwtDecode(idToken); // eslint-disable-line
    store.commit(`${APP}/setUser`, {
      id: decodedToken.sub,
      name: decodedToken.name,
    });
  }
};

let authToken: AuthToken = {};
const getAuthToken = (): AuthToken => {
  let authTokenStr;
  if (
    !Object.keys(authToken).length &&
    (authTokenStr = getSessionItem('authToken'))
  ) {
    authToken = JSON.parse(authTokenStr);
  }
  return authToken;
};

const resetAuthToken = () => {
  authToken = {};
  removeSessionItem('authToken');
};

const saveAuthToken = async (token: AuthToken) => {
  setUser(token.id_token as string);
  setSessionItem(
    'authToken',
    JSON.stringify(
      (authToken = {
        id_token: token.id_token, // eslint-disable-line
        access_token: token.access_token, // eslint-disable-line
        refresh_token: token.refresh_token, // eslint-disable-line
        expires_in: /* eslint-disable-line */ Number(
          `${(token.expires_in as number) * 1000 + Date.now()}`,
        ),
      } as AuthToken),
    ),
  );
};

const auth = new Vue({
  methods: {
    authorize(path: string) {
      // TODO: replace logic to use asymmetric keys for code challenge and code verifier instead of using the same value
      const codeVerfier = uniqid(44);
      const redirectUri = `${config.auth.redirectUrl}#${path}`;
      // TODO: consider removing / replacing "initialState" logic to redirect directly to the redirect_uri
      setSessionItem('initialState', redirectUri);
      setSessionItem('codeVerifier', codeVerfier);
      window.location.href =
        `${config.endpoints.authorize}?response_type=code&code_challenge=${codeVerfier}` +
        `&scope=${process.env.VUE_APP_AUTH_SCOPE}&state=${uniqid(11)}` +
        `&client_id=${config.auth.clientId}&redirect_uri=${redirectUri}`;
    },

    async checkPostAuth() {
      // first, check for (auth) code in the the response query parameters (user just authenticated)
      const authCode = getQueryParam('code');
      if (authCode) {
        resetAuthToken();
        setSessionItem('authCode', authCode);
        // restore the original path and push it on the window history
        window.history.replaceState(
          'callback',
          'callback',
          `${getSessionItem('initialState')}`,
        );
      }
    },

    async fetchTokens() {
      resetAuthToken();
      try {
        const response = await axios.post(
          config.endpoints.tokens,
          `grant_type=${process.env.VUE_APP_AUTH_GRANT_TYPE_AUTH}` +
            `&code=${getSessionItem('authCode')}` +
            `&code_verifier=${getSessionItem('codeVerifier')}` +
            `&redirect_uri=${config.auth.redirectUrl}&client_id=${config.auth.clientId}`,
        );
        await saveAuthToken(response.data);
        return response.data;
      } catch (e) {
        this.authorize('/');
      }
    },

    async forceAuthenticate() {
      resetAuthToken();

      // TODO: Not sure how to reproduce a forced authentication, if 'this.$messaging' does not work, simply import 'messaging' (from '@/shared-components/plugins/messaging)
      confirm('Session expired, refresh to reauthenticate.');
    },

    isAuthenticated(): boolean {
      if (this.isEnabled() && getSessionItem('authCode')) {
        return true;
      }
      return false;
    },

    isEnabled() {
      return process.env.VUE_APP_AUTH_ENABLED === 'true';
    },

    isExpired() {
      const exp = getAuthToken().expires_in;
      return exp && exp < Date.now();
    },

    async refreshTokens() {
      try {
        const response = await axios.post(
          config.endpoints.tokens,
          `grant_type=${process.env.VUE_APP_AUTH_GRANT_TYPE_REFRESH}` +
            `&refresh_token=${getAuthToken().refresh_token}` +
            `&client_id=${config.auth.clientId}&scope=${process.env.VUE_APP_AUTH_SCOPE}`,
        );
        await saveAuthToken(response.data);
        return response.data;
      } catch (e) {
        this.authorize('/');
      }
    },

    async token() {
      const token = getAuthToken();
      if (token.id_token && !this.isExpired()) {
        // bit of a *hack*, but (re)sets the user in the case the page was refreshed and id token still valud
        setUser(token.id_token);
        return token.id_token;
      } else if (!token.id_token) {
        const fetchedTokens = await this.fetchTokens();
        return fetchedTokens.id_token;
      } else if (token.refresh_token) {
        const refreshedTokens = await this.refreshTokens();
        return refreshedTokens.id_token;
      } else {
        this.forceAuthenticate();
      }
    },
  },
});

export default {
  install: async (Vue: any) => /* eslint-disable-line */ {
    if (auth.isEnabled()) {
      await auth.checkPostAuth();
      Vue.prototype.$auth = auth;
    }
  },
  token: auth.token,
  authorize: auth.authorize,
  isAuthenticated: auth.isAuthenticated,
};
